import { ActionPanel, Action, List, Form, showToast, Toast, open } from "@raycast/api";
import React, { useState } from "react";
import { execSync } from "child_process";
import { homedir } from "os";
import { join } from "path";
import { readFileSync, writeFileSync, existsSync, mkdirSync } from "fs";

const VAULT_PATH = join(homedir(), "Desktop", "Real Niggas Vault");
const VAULT_NAME = "Real Niggas Vault";
const TEMPLATES_FOLDER = "5. Templates";

interface NoteType {
  title: string;
  icon: string;
  mode: string;
  folder: string;
  defaultTitle: string;
  description: string;
}

const noteTypes: NoteType[] = [
  {
    title: "Create Task",
    icon: "✅",
    mode: "Task",
    folder: "3. Tasks",
    defaultTitle: "New Task",
    description: "Create a new task"
  },
  {
    title: "Create Note",
    icon: "📝",
    mode: "Note",
    folder: "4. Notes",
    defaultTitle: "Quick Note",
    description: "Create a quick note"
  },
  {
    title: "Create Journal Entry",
    icon: "📔",
    mode: "Journal",
    folder: "2. Journal",
    defaultTitle: "Daily Entry",
    description: "Create a journal entry"
  },
  {
    title: "Create Business Idea",
    icon: "💡",
    mode: "Business",
    folder: "1. Business",
    defaultTitle: "New Business Idea",
    description: "Capture a business idea"
  },
  {
    title: "Create Reference",
    icon: "📚",
    mode: "Reference",
    folder: "6. References",
    defaultTitle: "Source Material",
    description: "Save reference material"
  }
];

function sanitizeFilename(text: string): string {
  return text.replace(/[^\w\s-]/g, "").trim().substring(0, 60);
}

async function createNote(noteType: NoteType, title: string) {
  const toast = await showToast({
    style: Toast.Style.Animated,
    title: "Creating note..."
  });

  try {
    const now = new Date();
    const dateStr = now.toISOString().split("T")[0];
    const timestampPretty = `${dateStr} ${now.toTimeString().substring(0, 5)}`;

    const finalTitle = title.trim() || noteType.defaultTitle;

    // Load template
    const templatePath = join(VAULT_PATH, TEMPLATES_FOLDER, `${noteType.mode}.md`);
    let fileContent: string;

    if (existsSync(templatePath)) {
      fileContent = readFileSync(templatePath, "utf-8");
      fileContent = fileContent.replace(/\{\{CONTENT\}\}/g, finalTitle);
      fileContent = fileContent.replace(/\{\{DATE\}\}/g, timestampPretty);
    } else {
      fileContent = `# ${finalTitle}\n\n(Template '${noteType.mode}.md' not found)`;
    }

    // Prepare filename
    const targetFolder = join(VAULT_PATH, noteType.folder);
    const safeTitle = sanitizeFilename(finalTitle);

    let filename: string;
    if (noteType.mode === "Task") {
      filename = `${dateStr}.md`;
    } else {
      filename = `${safeTitle} - ${dateStr}.md`;
    }

    // Create folder if needed
    if (!existsSync(targetFolder)) {
      mkdirSync(targetFolder, { recursive: true });
    }

    const fullPath = join(targetFolder, filename);

    // Handle duplicates
    if (existsSync(fullPath)) {
      const timeSuffix = now.toTimeString().substring(0, 8).replace(/:/g, "");
      if (noteType.mode === "Task") {
        filename = `${dateStr}-${timeSuffix}.md`;
      } else {
        filename = `${safeTitle} - ${dateStr} ${timeSuffix}.md`;
      }
    }

    // Write file
    writeFileSync(join(targetFolder, filename), fileContent);

    // Open in Obsidian
    const relativePath = `${noteType.folder}/${filename}`;
    const obsidianUri = `obsidian://open?vault=${encodeURIComponent(VAULT_NAME)}&file=${encodeURIComponent(relativePath)}`;
    await open(obsidianUri);

    toast.style = Toast.Style.Success;
    toast.title = "✓ Created!";
    toast.message = filename;

  } catch (error) {
    toast.style = Toast.Style.Failure;
    toast.title = "Failed to create note";
    toast.message = String(error);
  }
}

function TitleForm({ noteType }: { noteType: NoteType }) {
  const [title, setTitle] = useState("");

  return (
    <Form
      actions={
        <ActionPanel>
          <Action.SubmitForm
            title="Create Note"
            onSubmit={async () => {
              await createNote(noteType, title);
            }}
          />
        </ActionPanel>
      }
    >
      <Form.TextField
        id="title"
        title="Title"
        placeholder={`Leave blank for "${noteType.defaultTitle}"`}
        value={title}
        onChange={setTitle}
      />
      <Form.Description text={`Creating ${noteType.mode} in ${noteType.folder}`} />
    </Form>
  );
}

export default function Command() {
  return (
    <List searchBarPlaceholder="Choose note type...">
      {noteTypes.map((noteType) => (
        <List.Item
          key={noteType.mode}
          icon={noteType.icon}
          title={noteType.title}
          subtitle={noteType.description}
          accessories={[{ text: noteType.folder }]}
          actions={
            <ActionPanel>
              {noteType.mode === "Task" ? (
                <Action
                  title="Create Task"
                  onAction={async () => {
                    await createNote(noteType, "");
                  }}
                />
              ) : (
                <Action.Push
                  title="Enter Title"
                  target={<TitleForm noteType={noteType} />}
                />
              )}
            </ActionPanel>
          }
        />
      ))}
    </List>
  );
}
